/* eslint-disable react-hooks/exhaustive-deps */
import { createContext, FC, useContext, useMemo, useState } from "react";

import BusinessApi from "Apis/BusinessApi";
import { useSnacks } from "Contexts/Snack";
import {
  BusinessItemInterface,
  BusinessProfileInterface,
} from "Shared/Interfaces/Business/BusinessProfile";

import { BusinessApiContextState, BusinessDataContextState } from "./types";

const BusinessData = createContext<BusinessDataContextState | undefined>(
  undefined
);
const BusinessAPI = createContext<BusinessApiContextState | undefined>(
  undefined
);

const BusinessProvider: FC<any> = ({ children }) => {
  const { setErrorMessage } = useSnacks();
  const [allBusiness, setAllBusiness] = useState<BusinessItemInterface[]>([]);
  const [selectedBusiness, setSelectedBusiness] = useState<
    BusinessProfileInterface | undefined
  >(undefined);
  const data: BusinessDataContextState = { allBusiness, selectedBusiness };
  const api = useMemo<BusinessApiContextState>(
    () => ({
      getAllUserBusiness: async () => {
        const res = await BusinessApi.getAllBusiness();
        if (!res.success) return setErrorMessage(res.message);
        return setAllBusiness(res.payload);
      },
      fetchDefaultBusiness: async () => {
        const res = await BusinessApi.getDefaultBusiness();
        if (!res.success) return setErrorMessage(res.message);
        return setSelectedBusiness(res.payload);
      },
      setAllBusiness,
      setSelectedBusiness,
    }),
    []
  );

  return (
    <BusinessData.Provider value={data}>
      <BusinessAPI.Provider value={api}>{children}</BusinessAPI.Provider>
    </BusinessData.Provider>
  );
};

export const useBusinessData = () => {
  const context = useContext(BusinessData);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of BusinessProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export const useBusinessApi = () => {
  const context = useContext(BusinessAPI);

  if (context === undefined) {
    throw Error(
      "Item must be used inside of BusinessProvider, otherwise it will not function correctly."
    );
  }

  return context;
};

export default BusinessProvider;
